"use client";
import Image from "next/image";
import React, { useEffect, useState } from "react";
import userAvatar from "../../../../public/resourses/userAvatar.svg";
import { Eye, MapPin } from "lucide-react";
import { useGetPublicProfileInfo } from "@/app/lib/postServices/postQueries";
import { useParams } from "next/navigation";
import SkeletonNotificationSettings from "@/app/components/ui/SkeletonNotificationSettings";
import type { Ad } from "@/app/types/authTypes";
import AdCard from "@/app/components/ui/AdCard";
import  placeholderPost  from "../../../../public/resourses/"

const Star = ({ filled }: { filled: boolean }) => (
  <svg
    width="20"
    height="20"
    viewBox="0 0 20 20"
    fill={filled ? "#FFD600" : "#E0E0E0"}
    xmlns="http://www.w3.org/2000/svg"
    style={{ display: "inline" }}
  >
    <path d="M10 15.27L16.18 19l-1.64-7.03L20 7.24l-7.19-.61L10 0 7.19 6.63 0 7.24l5.46 4.73L3.82 19z" />
  </svg>
);

const PublicStore = () => {
  const params = useParams();

  const [page, setPage] = useState(1);
  const pageSize = 20;
  const [ads, setAds] = useState<Ad[]>([]);
  const [hasMore, setHasMore] = useState(true);


  const username = params.username as string;
  const getUserInfo = useGetPublicProfileInfo(username , page , pageSize);
  const { data , isLoading , isFetching} = getUserInfo;
  const user = data?.results.user;
  useEffect(() => {
    if (data && page === 1) {
      setAds(data.results.posts);
      setHasMore(data.count > data.results.posts.length);
    } else if (data && page > 1) {
      setAds((prev) => [...prev, ...data.results.posts]);
      setHasMore(ads.length + data.results.posts.length < data.count);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);
  // حساب عدد النجوم
  const rating = user?.average_rating || 0;
  const ratingCount = user?.rating_count || 0;
  const stars = Array.from({ length: 5 }, (_, i) => i < Math.round(rating));



  if (isLoading && page === 1) return <SkeletonNotificationSettings />;

  return (
    <div className="w-full flex flex-row-reverse justify-between items-start px-8 py-6 gap-8 rtl">
      {/* Top Section */}
      <div className="flex w-full justify-between">
        {/* Right: User Info */}
        <div className="flex max-sm:flex-col items-start w-1/2 bg-red-300">
          <div className=" rounded-full overflow-hidden border-2  h-full border-gray-200 mb-2 bg-green-300 w-1/2">
            <Image
              src={userAvatar}
              alt="profile image"
              width={400}
              height={400}
            />
          </div>
          <div className="bg-pink-600 w-1/2 h-full">
            <div className="text-xl font-bold mb-1 text-right w-full">
              {user ? `${user.first_name} ${user.last_name}` : "---"}
            </div>
            <div className="flex items-center gap-1 mb-1">
              {stars.map((filled, idx) => (
                <Star key={idx} filled={filled} />
              ))}
            </div>
            <div className="text-xs text-gray-600 mt-1 text-right w-full">
              <span>(تقييم {ratingCount})</span>
              <span className="font-bold mx-1">{rating.toFixed(1)}</span>
            </div>
          </div>
        </div>

        {/* Left: Store Info */}
        <div className="bg-yellow-100 rounded-xl shadow p-6 w-1/2  flex flex-col gap-6 border border-gray-100">
          <div className="flex items-center gap-2 text-gray-600 text-lg">
            <MapPin size={20} />
            <span>{user?.city || "---"}</span>
          </div>
          <div className="flex items-center gap-2 text-gray-600 text-lg">
            <Eye size={20} />
            <span className="font-bold">{user?.total_views || 0}</span>
            <span>مشاهدة</span>
          </div>
          <div className="flex gap-3 mt-2">
            <button
              type="button"
              className="flex-1 py-2 border border-cgreen text-cgreen rounded-md font-semibold hover:bg-cgreen hover:text-white transition"
            >
              ابلاغ
            </button>
            <button
              type="button"
              className="flex-1 py-2 bg-cgreen text-white rounded-md font-semibold hover:bg-chgreen transition"
            >
              تقييم المتجر
            </button>
          </div>
        </div>
      </div>

      {/* Bottom Section */}
      <div>
      {ads.length > 0 && 
      <div className="h-full   rounded-md max-sm:mt-2 mb-10 ">
        <div className="grid gap-4 grid-cols-2 md:grid-cols-4 xl:grid-cols-5">
          {ads.map((ad: Ad) => (
            <AdCard
              key={ad.id}
              title={ad.title}
              description={ad.description}
              city={ad.city}
              price={parseFloat(ad.price)}
              published={ad.status}
              isFav={false}
              imageUrl={ad.cover_image ?? placeholderPost }
              id={ad.id}
              subcat={ad.subcategory}
              offer_type={ad.offer_type}
            />
          ))}
        </div>

        {/* زر تحميل المزيد */}
        {hasMore && (
          <div className="flex justify-center mt-5">
            <button
              onClick={() => setPage((p) => p + 1)}
              disabled={isFetching}
              className="px-6 py-2 rounded-full border bg-white text-gray-600 hover:bg-gray-100 font-semibold shadow"
            >
              {isFetching ? "...جاري التحميل" : "تحميل المزيد"}
            </button>
          </div>
        )}
      </div>
      }

      </div>
    </div>
  );
};

export default PublicStore; 